#osnova {
    margin: auto;
    width: 745px;
    height: 460px;
    display: flex;
    flex-Direction: column;
    background-Color: #15202B;
    box-Sizing: border-box;
    user-Select: none;
    border: 3px solid rgba(110, 120, 110, .5);
    border-Radius: 5%;
    margin-bottom: 25px;

}

#header{
    display: flex;
    padding: 15px 0px 3px 18px;
    color: white;
    font-Size: 18px;
}

#userPhoto {
    width: 80px;
    height: 80px;
    border-Radius: 50%;
    -Webkit-User-Drag: none;
}

#userData {
    display: flex;
    flex-Direction: column;
}

#userInfo {
    width: auto;
    height: 45px;
    display: flex;
    justify-content: space-evenly;
    align-Items: center;
    padding-Left: 10px;
    font-Family: Titillium Web;
}

#aName{
    text-Decoration: none;
    color: white;
    font-Size: 24px;
    font-Weight: bold;
}

#imageImage{
    padding-Right: 15px;
    float: right;
    align-Self: flex-end;
    justify-Self: flex-end;
    width: 620px;
    height: 300px;
    border-Radius: 40px;
    -Webkit-User-Drag: none;
}

#footerIcon{
    color: #8899A6;
    padding: 14px 0px 0px 170px;
    width: 70%;
    display: flex;
    justify-Content: space-between;
    font-Family: Titillium Web;
    font-Size: 19.5px;
}

#iconContainer {
    width: 65px;
    display: flex;
    align-Items: center;
    justify-Content: space-between;
}

#nickname {
   
    color: #8798A5;
    text-Decoration: none;
}

#content {
    width: 400px;
    height: 45px;
    display: flex;
    justify-Content: space-between;
    align-Items: center;
    padding-Left: 10px;
    font-Family: Titillium Web;    
    font-Weight: bold;   
}

#galochka {
    width: 22px;
    height: 22px;
    padding-Top: 5px;
    -Webkit-Use-rDrag: none;
}

#point{
    display: inline-flex;
    height: 40px;
    align-Items: flex-start;
    font-Size: 20px;
    color: #8798A5;
}

#messages{
    width: 21px;
    height: 20px;
    -Webkit-User-Drag: none;
    cursor: pointer;
}
#share{
    width: 23px;
    height: 20px;
    -Webkit-User-Drag: none;
    cursor: pointer;
}

#like{
    width: 21px;
    height: 20px;
    -Webkit-User-Drag: none;
    cursor: pointer;
}

#repost{
    width: 21px;
    height: 20px;
    -Webkit-User-Drag: none;
    cursor: pointer;
}



#date{
    color: #8798A5;
}
